@mixin globalSetupComponent {

	*,
	*::before,
	*::after {
		box-sizing: border-box; }

	html,
	body {
		min-width: 100%;
		min-height: 100vh;
		font-family: $fontFamily;
		font-size: 1rem; }

	body {
		min-width: $minBodyWidth; }

	p, ul, ol, dl, table,
	blockquote, pre, figure,
	hr {
		margin-top: 0;
		margin-bottom: 1rem;
		padding-top: 0;
		padding-bottom: 0; }

	h1, h2, h3, h4, h5, h6 {
		margin-top: 0;
		margin-bottom: 0.5rem; } }
